#demo-wrapper {
    display: flex;

    padding: 1%;
}

#demo-nav-wrapper {
    width: 25%;
    border: none;
}

#demo-nav {
    border: none;
    justify-self: flex-start;
    position: sticky;
    top: 80px;
    margin-left: -10px;
    margin-right: 10px;
    border-radius: 5px;
    background-color: var(--grey-7);
    box-shadow:1px 2px 10px 0px var(--grey-1);
    padding: 7px;
}

#demo-nav > h3 {
    color: var(--blue-2);
    margin-bottom: 0px;
}

hr {
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, var(--grey-1), var(--grey-1), var(--grey-1));
}

ul {
    padding-left: 10px;
}

.main-li, .sub-li {
    display: block;
}

.main-li {
    margin-top: 10px;
}

.main-li > a {
    color: var(--grey-1);
    font-weight: normal;
}

.sub-li::before {
    content: "- ";
    color: var(--grey-3);
}

.sub-li > a {
    color: var(--grey-3);
    font-weight: normal;
    font-size: small;
}

ul > li > a{
    text-decoration: none;
    font-weight: bold;
    scroll-margin-top: 80px;
}


#demo {
    /*height: 100vh;  not mobile friendly */
    border-radius: 5px;
    background-color: var(--grey-7);
    opacity: 1;
    width: 100%;
    align-self: center;
    border: none;
    justify-content: flex-start;
    box-shadow:1px 2px 10px 0px var(--grey-1);
    line-height: 1.5;
    padding: 15px;
    color: var(--blue-1)
}

#button-well-1 {
    display: flex;
    justify-content: space-evenly;

}

.button-1 {
    background-color: var(--blue-4);
    color: var(--blue-7);
    border-radius: 5px;
    border: none;
    box-shadow: 1px 1px 6px 0px var(--grey-1);
    font-size: 100%;
    padding: 5px;
}

.button-1:hover {
    background-color: var(--blue-3);
}

.button-2 {
    background-color: var(--grey-7);
    color: var(--blue-4);
    border-radius: 13px;
    border: 2px solid var(--blue-4);
    box-shadow: 1px 1px 4px 1px var(--text-color);
    font-size: 100%;
    padding: 5px;
}

.button-2:hover {
    background-color: var(--grey-6);
}

.button-3 {
    border: none;
    background-color: var(--grey-6);
    text-decoration: underline;
    color: var(--blue-4);
    font-size: 100%;
    padding: 5px;
}

.button-3:hover {
    color: var(--blue-2)
}

.color-well {
    display: flex;
    justify-content: space-between;
    padding: 15px;
    margin: 10px;
}

.color-swatch {
    height: 30px;
    width: 60px;
    box-shadow: 1px 1px 3px 1px black;
    border-radius: 3px;
